const state = {
  language: 'cn',
  boardStyle: {
    boardColor: "#F4D03F",
    lineColor: "#000000",
    lineWidth: 0.03,
    coordColor: "#000000",
    coordFontStyle: "",
    coordFontFamily: "sans-serif",
    starRadiusScale: 0.1,
    pieceBlack: "#000000",
    pieceWhite: "#FFFFFF",
    pieceStrokeWidth: 0.02,
    pieceStrokeBlack: "#000000",
    pieceStrokeWhite: "#000000",
    pieceScale: 0.96,
    indexColorBlack: "#FFFFFF",
    indexColorWhite: "#000000",
    indexColorLast: "#E74C3C",
    indexFontStyle: "bold",
    indexFontFamily: "sans-serif",
    indexScale: 0.45,
    lastStepColor: "#E74C3C",
    lastStepScale: 0.15,
    winlineWidth: 0.15,
    winlineColor: "#2E86C1",
    bestMoveColor: "#E74C3C",
    thinkingMoveColor: "#3FF476",
    thoughtMoveColor: "#3C5EE7",
    lostMoveColor: "#FDFEFE",
    bestMoveScale: 0.12,
    realtimeMoveScale: 0.1,
    selectionStrokeWidth: 0.08,
    selectionStrokeColor: "#E74C3C"
  },
  boardSize: 15,
  thinkTimeOption: 1,
  turnTime: 5000,
  matchTime: 120000,
  maxDepth: 64,
  maxKNodes: 0,
  nbest: 1,
  rule: 0,  // 规则：0-无禁手 1-无禁长连不赢 4-有禁手 5-无禁一手交换
  configIndex: 0,
  clickCheck: 0,  // 点击方式：0-直接落子 1-二次确认 2-滑动落子
  showCoord: true,
  showDetail: true,
  showIndex: true,
  showLastStep: true,
  showWinline: true,
  indexOrigin: 0,
  aiThinkBlack: false,
  aiThinkWhite: false,
}

const getters = {
  turnTime: state => {
    let turn = [state.turnTime, 5000, 120000, 0]
    return turn[state.thinkTimeOption]
  },
  matchTime: state => {
    let match = [state.matchTime, 180000, 900000, 0]
    return match[state.thinkTimeOption]
  },
}

const mutations = {
  setValue(state, payload) {
    state[payload.key] = payload.value
  },
  setBoardStyle(state, payload) {
    state.boardStyle[payload.key] = payload.value
  },
}

const actions = {}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
